import React from 'react'
import {render} from 'react-dom'
import Contact from './Contact.jsx';
export default class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {lang: "zh-CN"};
    }
    componentDidMount () {
        const win = window;
        this.setState({lang: window.navigator.userLanguage || window.navigator.language});
    }
    render () {
        const isZh = (this.state.lang == "zh-CN")?true:false;
        let cnBak = null;
        if (isZh) cnBak = <p className="beian-content"><span>备案号</span>：<a href="http://www.miitbeian.gov.cn">湘ICP备18006506号-2</a></p>;
        return (
            <footer className="footer text-muted">
                <Contact />
                <div className="container">
                {cnBak}
                </div>
            </footer>
        );
    };
}