import React from "react";
import { render} from 'react-dom';
import Footer from './themes/Footer.jsx';
import Contents from './themes/Contents.jsx';
import Header from './themes/Header.jsx';
import './styles/css/custom.scss';
import Loader from 'react-loader-spinner'

class App extends React.Component {
  constructor(props){
    super(props)
    this.state = { isLoading: true }
  }
  componentDidMount() {
    this.timerHandle = setTimeout(() => { 
      this.setState({isLoading: false})
    },1000)
  }
  componentWillUnmount(){
    if (this.timerHandle) {
      clearTimeout(this.timerHandle);
      this.timerHandle = 0;
    }
  }
  render() {
    const isLoading = this.state.isLoading;
    let contents;
    if (isLoading) {
      contents = <div className="d-flex align-items-center justify-content-center"><Loader  type="Puff" color="#00BFFF" height="5em"	width="5em"/></div>
    } else {
      contents = [<Header key="Header"/>, <Contents key="Contents"/>, <Footer key="Footer"/>]
    }
    return (
      contents
    );
  }
}
render(<App />, document.getElementById("root"));