import React from 'react';
import { render} from 'react-dom';
import logo from '../assets/logo.svg';
export default class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        };
        this.toggleOpenClick = this.toggleOpen.bind(this);
    }
    toggleOpen() {
        this.setState({ isOpen: !this.state.isOpen })
    };
    
    render() {
        const {isOpen} = this.state
        return (
          <nav className="navbar navbar-expand-lg navbar-light">
            <div className="container"> <img className="img-fluid " src={logo} alt="Moruy Cyber Team." />
            <div className="d-flex ml-auto">
                <button onClick={this.toggleOpenClick} className={"navbar-toggler" + (isOpen ? "": " collapsed")} type="button" data-toggle="collapse" data-target="#globalNavbar" aria-controls="globalNavbar" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
            </div>
            <div className={(isOpen ? '' : 'collapse ') + ' navbar-collapse'} id="globalNavbar">
              <ul className="navbar-nav ml-auto justify-content-end">
              <li className="nav-item m-nav-item">
              <a className="nav-link" target="_blank" alt="Google Reverse Proxy" href="https://g.cdn.moruy.cn">Google Mirror</a>
              </li>
              <li className="nav-item m-nav-item">
              <a className="nav-link" target="_blank" alt="Duckduckgo Reverse Proxy"  href="https://d.cdn.moruy.cn">Duckduckgo</a>
              </li>
              <li className="nav-item m-nav-item">
              <a className="nav-link"  alt="Proxy Pool" target="_blank" href="https://pp.moruy.cn">IP Pool</a>
              </li>
            </ul>
            </div>
          </div>
        </nav>
        );
    }
}